import React from 'react'
import { Link } from 'gatsby'
import { ModalRoutingContext } from 'gatsby-plugin-modal-routing'
import "../styles/styles.css"
import image1 from "../../static/MinuteHero/image1.png"
import image2 from "../../static/MinuteHero/image2.png"
import image3 from "../../static/MinuteHero/image3.png"

const ModalExamplePage = () => (
  <ModalRoutingContext.Consumer>
    {({ modal, closeTo }) => (
      <div>
        {modal ? (
          <Link to={closeTo} class="close">
            Close
          </Link>
        ) : (
          <header>
            <h1>
              Website Title
            </h1>
          </header>
        )}

        <h1>5 Minute Hero</h1>
        
        <img class="minhero" src={image1} alt="image1"/>
        <img class="minhero" src={image2} alt="image2"/>
        <img class="minhero" src={image3} alt="image3"/>

        <div>
          <p>
          The fund-raiser was related to the COVID-19 outbreak, and the theme for the game jam had been:” You have 5 minutes”.
          You have 5 minutes to play minigames to improve stats before fighting a boss.
          <br></br>
          <a href="https://www.dropbox.com/s/k9t4r6o1yszyle0/5Min.zip?dl=0"> Game Zip </a> <br></br>
          </p> 
        </div>
      </div>
    )}
  </ModalRoutingContext.Consumer>
)

export default ModalExamplePage